import React from "react";
import Navbar from "../components/Navbar";
import Title from "./Title";
import PhotoHeader from "../assets/images/header.webp";
import {
  IconCircleChevronDown,
  IconCircleChevronUp,
} from "@tabler/icons-react";
import Formation from "./Formation";
import Footer from "../components/Footer";

const Home = () => {
  const handleClickImg = () => {
    //! text-container transition
    container.style.opacity = 1;
    container.style.visibility = "visible";
    container.style.transition = "1s ease-in-out";
    container.style.transform = "none";
    //! Click img
    imgContainer.style.cursor = "none";
    imgContainer.style.transform = "translateX(-80%)";
    imgContainer.style.border = "3px solid black";
    imgContainer.style.borderRadius = "0 0 0 400px";
    imgContainer.style.transition = "0.8s ease-in-out";
  };

  return (
    <>
      <Title />
      <section className="home" id="home">
        <Navbar />
        <div className="home-container">
          <div className="image-header">
            <img
              src={PhotoHeader}
              alt="Alexandre hontcharouk web developpeur"
              onClick={handleClickImg}
              id="imgContainer"
            />
          </div>
          <div className="text-container-header" id="container">
            <div>
              <p id="text1">
                Moi cest Alexandre, nouveau depuis peu dans le monde du
                dveloppement web.
              </p>
            </div>
            <div id="text2">
              <p>
                Form par The Hacking Project avec plus de 1200h et le RNCP5/BAC+2
                (Dev Web), plus un stage de 2 mois en entreprise pour Miss
                Camille, cratrice de bijoux.
              </p>
            </div>
            <div id="text3">
              <p>
                Je veux me faire une place rapidement pour vous donner un aperu
                de qui je suis. Je nai peux tre pour linstant pas beaucoup de
                choses  vous montrer mais ces choses existent pourtant et je
                compte bien continuer  abreuver ma soif de connaissance dans ce
                milieu qui me plat de plus en plus  mesure que javance et que
                je code tous les jours sans relache.
              </p>
            </div>
            <div id="text4">
              <p>
                Jadore travailler en quipe car on ne peut que progresser grce
                aux autres. Mon but serait de pouvoir prendre les commandes
                dune quipe et devenir lead Dev dun projet pour partager mes
                connaissances et mener  bien le projet ensemble.
              </p>
            </div>
            <div id="text5">
              <p>
                Les technologies sur lesquelles jai travaill sont: <br /> 
                <strong>
                  Ruby et Rails, Html, CSS, SASS, Javascript, ReactJS.
                </strong>{" "}
              </p>
            </div>
            <div id="text6">
              <p>
                Mais je veux vraiment me spcialiser sur: 
                <strong> JAVASCRIPT/TYPESCRIPT/REACT/NodeJS/HTML/SASS</strong>
              </p>
            </div>
          </div>
        </div>
        <div className="arrow-down-home">
          <a href="#formation" id="arrow">
            <IconCircleChevronDown size={51} color="black" stroke={1} />
          </a>
        </div>
      </section>
      <section id="formation">
        <Formation />
        <div className="arrow-up-home">
          <a href="#home" id="arrow">
            <IconCircleChevronUp size={51} color="black" stroke={1} />
          </a>
        </div>
      <Footer/>
      </section>
    </>
  );
};

export default Home;
